import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';

const imgSectionStyle = {
  textAlign: 'center',
  margin: '40px 0'
};

const HtmlTooltip = withStyles(theme => ({
  tooltip: {
    backgroundColor: '#e0e0e0',
    maxWidth: 300,
    fontSize: '16px',
    border: '1px solid #b2b2b2',
    color: '#000',
    borderRadius: 0,
    padding: '4px'
  },
}))(Tooltip);

const citations = [
  '[1] J. P. Jones and L. A. Palmer, An evaluation of the two-dimensional Gabor filter model of simple receptive fields in cat striate cortex, Journal of neurophysiology, vol. 58, no. 6, pp. 12331258, 1987.',
  '[2] R. Mehrotra, K. R. Namuduri, and N. Ranganathan, Gabor filter-based edge detection, Pattern recognition, vol. 25, no. 12, pp. 14791494, 1992.',
  '[3] J. Wang, Z. Zhang, C. Xie, V. Premachandran, and A. Yuille, Unsupervised learning of object semantic parts from internal states of CNNs by population encoding, arXiv:1511.06855 [cs], Nov. 2016.',
  '[4] K. Greff, R. K. Srivastava, and J. Schmidhuber, Highway and Residual Networks learn Unrolled Iterative Estimation, arXiv:1612.07771 [cs], Mar. 2017.',
  '[5] C. Olah, A. Mordvintsev, and L. Schubert, Feature Visualization, Distill, vol. 2, no. 11, p. e7, Nov. 2017, doi: 10.23915/distill.00007.',
  '[6] Q. Yu, Y. Yang, F. Liu, Y.-Z. Song, T. Xiang, and T. M. Hospedales, Sketch-a-net: A deep neural network that beats humans, International journal of computer vision, vol. 122, no. 3, pp. 411425, 2017.',
  '[7] I. Shafkat, Intuitively Understanding Convolutions for Deep Learning, Medium, 07-Jun-2018. [Online]. Available: https://towardsdatascience.com/intuitively-understanding-convolutions-for-deep-learning-1f6f42faee1. [Accessed: 07-Feb-2020].',
  '[8] E. Ulberg, Visual Concepts in Sketch-A-Net, Dec-2019. [Online]. Available: https://ulberge.github.io/SketchANetClustering/.',
];

function CitationGenerator(citations) {
  const fns = citations.map((citation, i) => {
    return () => (
      <HtmlTooltip title={<React.Fragment>{citation}</React.Fragment>} interactive>
        <Button style={{ padding: 0, minWidth: 0, height: 0 }}><span style={{ color: '#1f7e9b' }}>[{i + 1}]</span></Button>
      </HtmlTooltip>
    )
  });
  return i => fns[i-1]();
}

function KernelTunerAboutText(props) {
  const cite = CitationGenerator(citations);

  return (
    <div>
      <p><i>The <b>kernel tuner</b> is a parametric tool that makes kernels for detecting important features in a line drawing.</i></p>

      <h4>Example usage</h4>
      <p>How is the kernel tuner used?</p>
      <p>Lets say I care about line drawings of boxes. First, I might draw a box in the test area. A box is made up of horizontal/vertical lines and corners at 90 degrees. So, I would select the line and corner types and limit the angles to two. This yields kernels that match the elements of a box. When applied to the drawing, the six kernels neatly label the parts of the box.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/scrn0.png" alt="Kernel Tuner screenshot of making kernels"
          style={{ height: '150px', marginRight: '20px' }}
        />
        <img
          src="./imgs/tuner/name3.png" alt="6 Kernels"
          style={{ height: '150px', marginRight: '20px' }}
        />
        <img
          src="./imgs/tuner/box.png" alt="a box drawing"
          style={{ height: '120px', marginBottom: '15px' }}
        />
        <img
          src="./imgs/tuner/names0.png" alt="Activations (0) for box"
          style={{ height: '100px', marginBottom: '25px' }}
        />
      </div>

      <p>Adjusting the size of the kernels, the width of the sine wave or the radius of the 2D Gaussian changes the character of the kernels. The different kernels yield different readings of the same box.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/k04.png" alt="Sample kernels with different settings"
          style={{ height: '100px', marginRight: '20px' }}
        />
        <img
          src="./imgs/tuner/out1.png" alt="thin output"
          style={{ height: '75px', marginBottom: '12px' }}
        />
        <img
          src="./imgs/tuner/out2.png" alt="med output"
          style={{ height: '75px', marginBottom: '12px' }}
        />
        <img
          src="./imgs/tuner/out4.png" alt="thick output"
          style={{ height: '75px', marginBottom: '12px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Different kernels that can be produced. (2) A variety of outputs.</small>
        </div>
      </div>

      <p>The tool can also be used to check how robust the kernels are to rotation. A slider rotates the images and recalculates the activations in real time.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/rot.png" alt="Kernel Tuner screenshot of rotation"
          style={{ height: '300px' }}
        />
      </div>

      <p>The square above is no longer well matched by the kernels generated. There are blank spaces and poorly matched labels along the lines. To help diagnose what is going wrong, the tool provides information when the user hovers over a location. On click, the tool shows a chart of the top activations at the location and what the kernels look like as overlays at that point.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/selection.png" alt="Kernel Tuner screenshot of selection"
          style={{ height: '300px' }}
        />
      </div>

      <p>After inspection, we can see that our filters are quite relaxed. The corner is matching a corner at a different angle with high activation. We may want this, or we may want to increase the size of the Gaussian to make the filters more strict. Or, we can increase the angle setting to 4 to better match the corner.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/angles4.png" alt="Kernel Tuner screenshot of more angles provided"
          style={{ height: '300px' }}
        />
      </div>

      <h4>Abstraction through convolution</h4>

      <p>(<i>Note: A kernel is a 2D matrix of weights. A filter is a stack of kernels, with one kernel for each input channel. Since the first layer of a black and white image has only one channel, each filter has one kernel and we can use filter and kernel interchangeably.</i>)</p>

      <p>The kernel tuner produces kernels that translate an image from pixel values to higher order features such as lines, line ends, corners, T, X and Y-intersections. It does this through a process called convolution in which the kernels are applied as sliding windows across an image.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/slidingwindow.gif" alt="sliding window animation for convolutions"
          style={{ height: '200px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) The sliding window of a convolution {cite(1)}.</small>
        </div>
      </div>

      <p>After these kernels are applied, each pixel location is given a more meaningful name like vertical line or T-intersection at 45 degrees. In reality, they are being given values corresponding to each name, but we can think of the channel with the highest value as the locations assigned name.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/name2.png" alt="line drawing of a box"
          style={{ height: '40px', marginBottom: '80px', marginRight: '40px' }}
        />
        <img
          src="./imgs/tuner/name3.png" alt="6 kernels for a box line drawing"
          style={{ height: '200px', marginRight: '40px' }}
        />
        <img
          src="./imgs/tuner/name0.png" alt="graphics of max activations for a box line drawing"
          style={{ height: '200px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Raw pixel data. (2) Set of kernels. (3) Map of maximum activations.</small>
        </div>
      </div>

      <p>By extracting this information, we can interact with the canvas at a higher level of abstraction. Convolutional neural networks use this process to classify images as abstract entities. The prevailing wisdom is that each layer of a network produces a higher order of information than the previous layer {cite(5)}. This may or may not be the case, but it is at least a useful starting point for thinking about how networks learn {cite(4)}.</p>

      <p>At the first layer, filters function as templates. They may be fuzzy, but they match a narrow range of pixel arrangements. After multiple layers the filters no longer correspond to templates. The network can match a visual object at different scales, rotations, or individual expressions (to some extent). It should be noted that if something can be matched with a template, it only requires one layer of convolution. If an object is abstract (i.e. it can take a variety of forms), then the network must flexibly encode the visual concept through multiple layers.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/match2.png" alt="line drawing of a corner"
          style={{ height: '80px' }}
        />
        <img
          src="./imgs/tuner/match0.png" alt="6 kernel overlays of corner line drawing"
          style={{ height: '80px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Kernels overlaid on corner.</small>
        </div>
      </div>

      <h4>Background</h4>
      <p>The patterns for the kernels produced by this tool are inspired by Gabor filters. Gabor filters are kernels created using a 2D Gaussian function applied to a sine wave {cite(2)}. They are useful for edge detection (or in this case, line detection). A study by Jones and Palmer demonstrated that 2D Gabor filters provide similar performance to the simple receptive fields in a mammalian visual cortex {cite(1)}. As further demonstration of their representational capacity, a convolutional neural network trained on a dataset of line drawings learned filters that looked eerily similar to Gabor filters {cite(6)}.)</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/func0.png" alt="1D Gabor filter"
          style={{ height: '100px', marginRight: '20px' }}
        />
        <img
          src="./imgs/tuner/gaborplus2.jpg" alt="kernels in Sketch-A-Net"
          style={{ height: '100px', marginRight: '20px' }}
        />
        <img
          src="./imgs/tuner/gaborplus.jpg" alt="Gabor filters"
          style={{ height: '100px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) 1D Gabor filter. Red: sine wave, Green: 1D Gaussian (also known as a normal distribution), Blue: Gabor filter. (2) A bank of Gabor filters at different sizes and rotations {cite(6)}. (3) Kernels from the first layer of Sketch-A-Net {cite(6)}.</small>
        </div>
      </div>

      <h4>Construction of kernels</h4>
      <p>Typically, Gabor filters are applied at various sizes (as seen in the bank above). This tool is designed to work with line drawings of a consistent stroke width and therefore provides a single size. In addition, this tool produces filters that are cut off after a single traversal of a sine wave. Gabor filters have multiple ridges to match textures in addition to lines.</p>
      <p>(For simplicity's sake, this project ignores textures and focuses on detecting line features. Even though textures play an important role in some styles of line drawings.)</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/gaborme.png" alt="vertical line filter generated with tuner"
          style={{ height: '150px' }}
        />
        <img
          src="./imgs/tuner/gabor1.jpg" alt="vertical Gabor filter"
          style={{ height: '150px', marginLeft: '20px' }}
        />
        <img
          src="./imgs/tuner/kerneltypes.png" alt="sample of line filters generated with tuner"
          style={{ height: '100px', marginBottom: '25px', marginLeft: '20px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Vertical line filter generated with Kernel Tuner. (2) Vertical Gabor filter. (3) Other types of kernels generated.</small>
        </div>
      </div>

      <p>The other important difference is the range of features matched. Instead of being limited to lines of different orientations, the kernels produced can match a range of feature types. Normally, networks take their time to build up these representations. In an investigation of the representations at different layers within Sketch-a-Net (which has convolutional layers), I found that the first layer matched line orientations {cite(8)}. Layers two through four haphazardly refined features such as corners, rounded corners, dots, curves, and a variety of textures. </p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/viz0.png" alt="Sketch-A-Net layer 1 visual concepts"
          style={{ maxWidth: '500px', width: '70%' }}
        />
        <img
          src="./imgs/tuner/viz1.png" alt="Sketch-A-Net layer 2 visual concepts"
          style={{ maxWidth: '500px', width: '70%' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1-4) Illustrations of visual concepts in Layers 1 and 2 in Sketch-A-Net, found using a K-Means clustering approach borrowed from {cite(3)}. More can be seen <a href="https://ulberge.github.io/SketchANetClustering/">here</a></small>
        </div>
      </div>

      <p>While the machine derives representations it finds useful from scratch, a human can define the important features of a line drawing logically. Namely, we know we care about the orientation of the lines and their intersections. We could define other features of interest, but the number of filters could quickly get out of control. The approach of this tuner is to incorporate human common sense while leaving further features to be derived later on.</p>

      <p>The kernel types were chosen to span the range of possibilities for one, two, or three lines that pass from outside the kernel window through (or to) the center of the kernel window. Typically CNNs learn to detect features at different offsets as well, but I chose to ignore that until I can be convinced it is necessary. The goal is to efficiently extract the most salient, template-like information available. Subsequent layers will condense and make sense of it.</p>

      <p>The construction of these kernels is similar to Gabor filters but with a domain limited to PI. Furthermore, each kernel is a Frankenstein of Gabor filters corresponding to the lines involved. Most are composed of multiple lines stitched together.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/linetypes.png" alt="The one, two, and three line constructions considered"
          style={{ height: '200px', filter: 'grayscale()' }}
        />
        <img
          src="./imgs/tuner/stitch.png" alt="The L corner is made by stitching perpendicular Gabor filters along a diagonal a 45 degree angle"
          style={{ height: '100px', marginBottom: '50px', marginLeft: '40px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) The one, two, and three line constructions considered. (2) The L corner is made by stitching together Gabor filters.</small>
        </div>
      </div>

      <h4>Head-to-head comparison with first layer kernels in machine learning</h4>
      <p>To verify that these kernels encode sufficient information to act as a foundation for robust networks, I built models on top of them. Then, I compared the classification accuracy with other approaches. I tested models with the kernels as the fixed, pre-trained weights and compared them with models using random, trainable weights. For this experiment, I used the TU Berlin sketch dataset, augmented through shifting the images and through slight shear, zoom, and rotational transformations. I also compare the results to state-of-the-art approaches.</p>

      <h4>Test #1</h4>
      <p>The first network I built was designed to classify using simple statistics built on top of the Kernel Tuner filters. My hope was to establish a baseline of how much information was being extracted through just the kernels. The network pools the information and uses a fully connected layer before the final softmax layer to calculate statistics.</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/stats.png" alt="Model summary for Test #1 of stats on top of kernels"
          style={{ height: '300px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Model summary for Test #1 of statistics on top of kernels.</small>
        </div>
      </div>

      <h4>Test #2</h4>
      <p>Secondly, I built a shallow CNN on top of the kernels to see how they would compare to a network without pretrained weights and to state of the art results on the same dataset. While this network has over 3 million trainable weights, it is still considerably smaller than the 8.5 million of Sketch-A-Net {cite(6)}</p>

      <div style={imgSectionStyle}>
        <img
          src="./imgs/tuner/conv.png" alt="Model summary for Test #2 of shallow CNN on top of kernels"
          style={{ height: '500px' }}
        />
        <div style={{ textAlign: 'center' }}>
          <small>(1) Model summary for Test #2 of shallow CNN on top of kernels.</small>
        </div>
      </div>

      <h4>Results</h4>
      <p>My preliminary results show a % for the shallow statistical network and % for the shallow CNN. When I replaced the kernels with random weights the results were % and % respectively. Sketch-A-Net achieved %, although with more weights and a more complex model. I would argue that the use of kernels also improved the generalizability since there was a concerted approach to have a well distributed set of features recognized.</p>

      <h4>Conclusion</h4>
      <p>The kernel tuner provides a way to tune kernels for line drawings. It acts as a visual debugger to make the filters for the first convolutional layer to translate from pixels to visual concepts.</p>
    </div>
  );
}


export default function KernelTunerAbout(props) {
  return (
    <ExpansionPanel defaultExpanded={true} TransitionProps={{ mountOnEnter: true }} square={true} style={{ boxShadow: 'none', border: '1px solid #b2b2b2' }}>
      <ExpansionPanelSummary
        expandIcon={<ExpandMoreIcon />}
        aria-controls="kerneltuner-content"
      >
        <h3 style={{ margin: '0 10px' }}>About</h3>
      </ExpansionPanelSummary>
      <ExpansionPanelDetails style={{ margin: '0px 20px' }}>
        <KernelTunerAboutText />
      </ExpansionPanelDetails>
    </ExpansionPanel>
  );
}
